export default class VideoPlayer {
  constructor(triggers, overlay) {
    this.btns = document.querySelectorAll(triggers);
    this.overlay = document.querySelector(overlay);
    this.close = this.overlay.querySelector('.close');
  }

  // перебираем все кнопки и навешиваем событие, где из каждой кнопки мы выбираем аттрибут со ссылкой на видео и создаем плеер (создание не есть инициализация!!)

  bindTriggers() {
    this.btns.forEach(btn => {
      btn.addEventListener('click', () => {

        if (document.querySelector('iframe#frame')) { // если player уже был открыт, то мы не создаем новый player, а открываем предыдущий player
          this.overlay.style.display = 'flex';
        } else { // если player еще ни разу не был открыт, то мы создаем новый player
          const path = btn.getAttribute('data-url');

          this.createPlayer(path);
        }
      });
    });
  }

  // кнопка закрытия видео (убираем модальное окно и останавливаем player)

  bindCloseBtn() {
    this.close.addEventListener('click', () => {
      this.overlay.style.display = 'none';
      this.player.stopVideo();
    });
  }

  // api - создаем player

  createPlayer(url) {
    this.player = new YT.Player('frame', {
      height: '100%',
      width: '100%',
      videoId: `${url}`
    });

    console.log(this.player);
    this.overlay.style.display = 'flex';
  }

  //

  // инициализируем player

  init() {

    // подключаем скрипт (api)

    const tag = document.createElement('script');

    tag.src = 'https://www.youtube.com/iframe_api';
    const firstScriptTag = document.getElementsByTagName('script')[0];
    firstScriptTag.parentNode.insertBefore(tag, firstScriptTag);

    //

    this.bindTriggers();
    this.bindCloseBtn();
  }
}